<script>
import ImageList from "./ImageList/ImageList";
import Zoom from "./Zoom/Zoom";
import { mapGetters } from "vuex";

export default {
  name: "Detail",
  components: {
    ImageList,
    Zoom,
  },
  created() {
    const { skuId } = this.$route.params;
    this.$store.dispatch("getProductDetailInfo", skuId);
  },
  computed: {
    ...mapGetters([
      "categoryView",
      "skuInfo",
      "skuImageList",
      "spuSaleAttrList",
      "valuesSkuJson",
    ]),
  },
  data() {
    return {
      selectedImg: 0,
      selectedValuesSkus: [],
      skuNum: 1,
      skuId: this.$route.params.skuId,
    };
  },
  methods: {
    setImgIndex(index) {
      this.selectedImg = index;
    },
    setSaleAttr(item, spuSale, index) {
      const { spuSaleAttrValueList } = spuSale;
      spuSaleAttrValueList.forEach((element) => element.isChecked = "0");
      item.isChecked = "1";
    },
    addToCart() {

      const skuId = this.skuId;
      const skuNum = this.skuNum; 
      const locaton = {
        name:'addcartsuccess',
        query:{skuNum}
      }
      this.$store.dispatch('changeShopCart',{skuId,skuNum})
      .then((result) => {
        // æååè·³è½¬
        // ç¨sessionStorageä¼ éååä¿¡æ¯
        window.sessionStorage.setItem('SKUINFO_KEY',JSON.stringify(this.skuInfo));
        this.$router.push(locaton);
      }).catch((err) => {
        // å¤±è´¥æç¤º
        console.log('æ·»å è´­ç©è½¦å¤±è´¥',err);
      });
      
    },
  },
};
</script>

